*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

/*loader  */
.spinner {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
    background-color: rgba(0, 0, 0, 0.383);
}
.form-btn{
    width: 250px !important;
    margin-top: 30px !important;
}
.main {
    padding: 10px;
    height: 100vh;
    
}

.layout{
    display: flex;

}

.sidebar {
    min-height: 100%;
    width: 300px;
    border-radius: 5px;
    background-color: rgb(24,69,59);
    box-shadow: 0 0 2px gray;
    margin-right: 20px;
    color: white;
}

.content{
    width: 100%;
    height: 100%;
}

.header{
    height: 10vh;
    box-shadow: 0 0 2px gray;
    margin-bottom: 20px;
    background-color: white;
}
.body{
    height: 85vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px gray;
    background-color: white;
}
.logo h2 {
   font-size: 1.0rem ;
   /*text-align: center;*/
   font-weight: bold;
   margin: 20px 0px; 
}

.menu{
    margin-top: 50px;
}
.menu-item{
    margin-top: 30px;
}
.menu-item a{
    color:white;
    text-decoration: none;
    font-size: 1.2rem;

}
.menu-item i{
    font-size: 1.2rem;
    margin: 0 15px;
}
.active{
    background-color: white;
    padding: 10px;
}
.active a, .active i{
    color: rgb(51,1,1);
}
.header-content{
    display: flex;
    align-items: center;
    height: 60px;
    justify-content: flex-end;
    margin: 0 15px;
}
.header-content i{
    margin-right: 20px;
    font-size: 1.2rem;
}
.header-content a{
    text-decoration:none ;
    color: brown;
    font-size: 1.2rem;
    margin: 0 10px;
    text-transform: uppercase;
}

